import React from "react";

const Comment = ({comment}) => (
  <tr className="comment-card">
    <td className="comment-td">
      <div className="writer-img" style={{backgroundUrl: `url('${comment.mentor_img}')`}} />
    </td>
    <td className="comment-td">
      <div className="comment-content">{comment.content}</div>
      <div className="comment-info">
        <div className="comment-writer">{comment.mentor_name || "어썸스쿨"}</div>
        <div className="write-date">{comment.created_at}</div>
      </div>
    </td>
  </tr>
)

export default Comment
